import * as types from './types';
import {
  UserInfoReq,
  UserLogoutReq
} from '../../../common/rest';

export default {
  // 
  loadUserInfo({commit}) {
	  UserInfoReq().then(data => {
      commit(types.LOAD_USER_INFO, data.data);
    });
  },
  // 
  clearUserInfo({commit}) {
    UserLogoutReq.then(data => {
      commit(types.CLEAR_USER_INFO);
      this.$router.push("/user/login");
    });
  }
}



